import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { ExpenseA4Component } from 'src/app/sales/components/print/expenses/expense-a4/expense-a4.component';
import { ExpensesA5Component } from 'src/app/sales/components/print/expenses/expenses-a5/expenses-a5.component';
import { ExpenseK80Col4Component } from 'src/app/sales/components/print/expenses/expense-k80-col4/expense-k80-col4.component';
import { ExpenseK80Col3Component } from 'src/app/sales/components/print/expenses/expense-k80-col3/expense-k80-col3.component';
import { ExpenseK57Col3Component } from 'src/app/sales/components/print/expenses/expense-k57-col3/expense-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-detail',
  templateUrl: './detail.component.html',
  styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {
  @ViewChild('nameGroup') nameGroup; // bin ly tn nhm (form kg cho dng ngmodel)
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  data: any;
  check: boolean = false; // bin bt load d liu khi xa hoc cp nht phiu chi
  type_expense: any;
  radioValue: any;
  detailExpensesForm: FormGroup;
  money_currency: any;
  list_suppliers: any = [];
  listPaymentBank: any = [];
  disabled = true;
  isDisabled: boolean = false; //bin c true khi phiu  trng thi  hy
  expendGroup: any = this.vhQuerySales.getlocalSpending_groups() // nhm chi
  /**Gii hn thi gian cho to bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < (new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000)) return true
    return false
  }
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhComponent: VhComponent,
    public languageService: LanguageService,
    private vhQuery: VhQuery,
    private vhAuth: VhAuth,
    private fncService: FunctionService
  ) {
    this.data = this.router.getCurrentNavigation().extras.state.data;
    this.type_expense = this.data.bill_type;
    if (this.type_expense == 26 || this.type_expense == 28) {
      this.isDisabled = true;
    }
    if (this.data) this.initForm()
  }

  ngOnInit(): void {
    this.list_suppliers = this.vhQuerySales.getlocalSuppliers();
  }

  ngAfterViewInit() {
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }

  /**
   * ly cc gi tr ca bill khi to cho form
   * @example initForm()
   */
  initForm() {
    this.detailExpensesForm = new FormGroup({
      date: new FormControl(this.data.date),
      customer: new FormControl({ value: this.data.bill_type == 12 || this.data.bill_type == 28 ? this.data.id_supplier : this.data.name, disabled: this.isDisabled }, Validators.compose([Validators.required])),
      total: new FormControl({ value: this.data.total, disabled: this.isDisabled }, Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl({ value: this.data.content, disabled: this.isDisabled }),
      payment_type: new FormControl(this.data.payment_type),
      wallet: new FormControl(this.vhQuerySales.getlocalWallet(this.data['id_wallet'])),
      type_receipt: new FormControl(this.data.bill_type),
      id_group: new FormControl({ value: this.data.bill_type == 8 || this.data.bill_type == 26 ? this.data.id_group : null, disabled: this.isDisabled }),
    });
    this.radioValue = this.data.payment_type.toString();
  }
  /**
 * bt sk chn ngy to bill v check validate hp l
 * @param $event date
 * @example this.changeDate('2023-06-18T16:27:16.276Z')
 */
  changeDate(event) {
    this.vhQuerySales.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.detailExpensesForm.controls['date'].setValue(new Date())
        }
        else {
          this.updateBill({ date: this.detailExpensesForm.value.date })
          this.data.date = event
        }
      })
  }

  
  /** tr v trang danh sch phiu chi
   * @example this.goBack()
   */
  goBack() {
    let data = { ...this.dataRestore.dataRestore }
    data.check = true
    this.router.navigate(['/sales/dashboard/manage/cashflow/expenses'], {
      state: this.check ? data : this.dataRestore.dataRestore
    });
  }


  /**Zorro modal */
  isVisible = false;
  /**
   * m modal hin th xc nhn xa phiu chi
   * @param value : bin c check in hay ko
   * @example this.showModal(true)
   */
  showModal(): void {
    this.isVisible = true;
  }
  /**
   * bt sk nhn nt ok ca modal
   * @example this.showModal(true)
   */
  handleOk(): void {
    this.isVisible = false;
    if (this.vhAuth.checkMyPermission("cashflow_cancel_expenses")) this.deleteExpenses();
    else this.vhComponent.showToast(1500, this.languageService.translate('You do not have this rights'), "alert-toast")
  }
  /**
   * bt sk nhn nt hy ca modal
   * @example this.showModal(true)
   */
  handleCancel(): void {
    this.isVisible = false;
  }

  /**Xa phiu chi khi DB
   * @example this.deleteExpenses()
  */
  deleteExpenses() {
    this.vhComponent.showLoading("").then(() => {
      this.vhQuerySales
        .deleteBill(this.data['_id'])
        .then((res: any) => {
          this.check = true
          this.vhComponent.hideLoading(0);
          this.vhComponent.showToast(1000,
            (this.languageService.translate("Expense has been deleted successfully")) + " " + this.data.bill_code, "success-toast"
          );
          setTimeout(() => {
            this.goBack();
          }, 200);
        }, err => { })
    })
  }

  /** 
   * bt thay i input total, cp nht li total v payment trong bill
   * @example this.updateTotal()
  */
  updateTotal() {
    if (this.money_currency.getRawValue() != "")
      this.updateBill({ total: parseInt(this.money_currency.getRawValue()), payment: parseInt(this.money_currency.getRawValue()) })

  }

  /**Update bill trn DB
   * @param data : cc trng cn cp nht
   * @example this.updateBill({total : 100000})
   */
  updateBill(data) {
    this.vhQuerySales.getValidityDate(this.detailExpensesForm.value.date)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.initForm()
        }
        else {
          if (this.type_expense == 12) data['id_supplier'] = data['customer']
          else { data['name'] = data['customer'] }
          this.check = true
          data['payment_type'] = this.data.payment_type;
          data['total'] = parseInt(this.money_currency.getRawValue());
          data['payment'] = parseInt(this.money_currency.getRawValue());
          if (this.detailExpensesForm.valid) {
            this.vhQuerySales.updateBill(this.data['_id'], data)
              .then(res => {
              })
              .catch(err => console.error('update failed => ' + err))
          }
        }
      })


  }

  /** kim tra d liu my in v show component in tng ng
  * @example this.showPrint() 
  */
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.data['content'] = this.detailExpensesForm['value'].content
        this.data['supplier'] = [12, 28].includes(this.type_expense) ? this.vhQuerySales.getlocalSupplier(this.data['id_supplier']) : this.detailExpensesForm['value'].customer
        this.data['employee'] = this.vhAuth.getlocalEmployee(this.data['id_employee'])
        this.data['total'] = parseInt(this.money_currency.getRawValue())
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), {
          billInfo: this.data,
          printer: printer,
        }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { })
          })
      }
    }, (err) => {
      let name: any = 'Pay for liabilities / others'
      this.vhComponent.alertMessage(this.languageService.translate("Function"), "", this.languageService.translate("Please turn on the printer function"), "OK")
        .then(() => this.router.navigate(['/sales/dashboard/manage/printer', 'page_desktop_expense'], { state: name }))
    })
  }
  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExpenseK57Col3Component
      case 'print_size_k80_3c': return ExpenseK80Col3Component
      case 'print_size_k80_4c': return ExpenseK80Col4Component
      case 'print_size_a5': return ExpensesA5Component
      case 'print_size_a4': return ExpenseA4Component
      default: return ExpenseA4Component
    }
  }
  /*----------Print------------------*/
  /**
  * ly thng tin d liu trang in
  * @returns Promise<any>
  * @example this.checkPrint()
  */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_expense')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }


  /***********Thm xa sa nhm chi********** */
  isVisiblelist: boolean = false; // popup danh sch nhm
  activeButton: boolean = false
  id_group_default: any = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
  defaultGroup = this.id_group_default.length ? this.id_group_default[0]._id : null
  /**
  * to nhm chi ln DB
  * @param nameGroup : tn nhm chi khc
  * @example this.addGroup('Tin mua ni tht')
  */
  addGroup(nameGroup): void {
    let index = this.expendGroup.findIndex(item => item.default)
    this.activeButton = true
    let value = {
      name: nameGroup.value,
      default: index != -1 ? false : true
    }
    if (value.name != "") {
      this.vhQuerySales.addSpending_group(value).then((res) => {
        this.expendGroup = [...this.expendGroup, res];
        this.nameGroup.nativeElement.value = ' ';
        this.activeButton = false
      })
    } else this.vhComponent.alertMessageDesktop("warning", "Cha nhp tn nhm")
  }
  /**
   * Cp nht trng default ca nhm chi
   * @param item : nhm chi  chn
   * @example this.setDefault(spending_group)
   */
  setDefault(item) {
    let index = this.expendGroup.findIndex(item => item.default)
    item.default = true
    if (index != -1) {
      this.expendGroup[index].default = false;
      Promise.all([this.vhQuerySales.updateSpending_group(item._id, item), this.vhQuerySales.updateSpending_group(this.expendGroup[index]._id, this.expendGroup[index])])
        .then(() => { })
    } else {
      this.vhQuerySales.updateSpending_group(item._id, item).then(() => { })
    }
  }
  /**
   * m modal chn nhm chi
   * @example this.openLisGroup()
   */
  openLisGroup() {
    this.id_group_default = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
    this.defaultGroup = this.id_group_default.length ? this.id_group_default[0]._id : null
    this.isVisiblelist = true
  }
  /**
   * tt modal chn nhm chi
   * @example this.handleCancellist()
   */
  handleCancellist() {
    this.isVisiblelist = false;
  }

  changeEdit: any = ''
  /**
  * gn _id ca nhm chi  chn vo bin changeEdit  update css  giao din
  * @param _id : _id ca nhm chi  chn
  */
  editGroup(_id) {
    this.changeEdit = _id
  }
  /**
   * cp nht tn nhm chi
   * @param item nhm chi  chn
   * @example this.saveEditGroup(item)
   */
  saveEditGroup(item) {
    let value = {
      name: item.name
    }
    this.vhQuerySales.updateSpending_group(item._id, value).then(() => {
      this.changeEdit = ""
    })
  }


  /**
   * xa nhm chi ln DB v local
   * @param _id : _id ca nhm chi  chn
   * @param index : v tr ca nhm chi trong mng local
   * @example this.deleteGroup('BDUWYHDIWUFRIWUHDE', 1)
   */
  deleteGroup(_id, index) {
    this.vhQuerySales.deleteSpending_group(_id).then((bool) => {
      if (bool) this.expendGroup.splice(index, 1)
      else this.vhComponent.alertMessageDesktop("error", "Khng xa c nhm ny")
    })
  }
}
