import { Component, OnInit, ViewChild } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { NavController } from '@ionic/angular';
import { VhAlgorithm, VhAuth, VhQuery, VhQuerySales } from 'ionic-vhframeworks';
import { ExpenseA4Component } from 'src/app/sales/components/print/expenses/expense-a4/expense-a4.component';
import { ExpensesA5Component } from 'src/app/sales/components/print/expenses/expenses-a5/expenses-a5.component';
import { ExpenseK80Col4Component } from 'src/app/sales/components/print/expenses/expense-k80-col4/expense-k80-col4.component';
import { ExpenseK80Col3Component } from 'src/app/sales/components/print/expenses/expense-k80-col3/expense-k80-col3.component';
import { ExpenseK57Col3Component } from 'src/app/sales/components/print/expenses/expense-k57-col3/expense-k57-col3.component';
import { VhComponent } from 'src/app/sales/components/vh-component/vh-component';
import { FunctionService } from 'src/app/sales/services/function.service';
import { LanguageService } from 'src/app/sales/services/language.service';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {
  @ViewChild('nameGroup') nameGroup; // bin ly tn nhm (form kg cho dng ngmodel)
  dataRestore: any = this.router.getCurrentNavigation().extras.state;
  listPaymentBank: any = [];
  list_suppliers: any = [];
  money_currency: any;
  radioValue: any = 'A';
  addExpensesForm: FormGroup;
  type: any = 12; // Bin lu loi phiu chi c chn -> 12: Phiu chi cng n nh cung cp
  nzOptions: any;
  billInfo: any = { employee: this.vhAuth.getUser() };
  expendGroup: any = this.vhQuerySales.getlocalSpending_groups() // nhm chi
  /**Gii hn thi gian cho to bill */
  disabledDate = (current: Date): boolean => {
    if (current.getTime() < (new Date(this.vhAuth.getStore().registrationdate).getTime() - 24 * 60 * 60 * 1000)) return true
    return false
  }
  constructor(
    private router: Router,
    public vhAlgorithm: VhAlgorithm,
    private vhQuerySales: VhQuerySales,
    private vhAuth: VhAuth,
    private vhComponent: VhComponent,
    private vhQuery: VhQuery,
    public languageService: LanguageService,
    private navCtrl: NavController,
    private fncService: FunctionService
  ) {
    this.getPaymentBankList();
    let options = [
      {
        value: '1',
        label: this.languageService.translate('Cash'),
        isLeaf: true
      },
      {
        value: '3',
        label: this.languageService.translate('Account'),
        children: this.listPaymentBank
      }
    ]
    this.nzOptions = options;
  }
  checkPrinter: boolean = false;
  ngOnInit(): void {
    this.list_suppliers = this.vhQuerySales.getlocalSuppliers();
    this.initForm();
    let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_expense')

    if (printer) this.checkPrinter = printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable;
    this.expendGroup = this.vhQuerySales.getlocalSpending_groups()
  }
  ngAfterViewInit() {
    // to cleavejs cho input price
    this.vhAlgorithm.waitingStack().then(() => {
      this.money_currency = this.vhAlgorithm.vhnumeral(".price");
    });
  }

  /**
   * khi to form c cc trng c required trong bill
   * @example initForm()
   */
  initForm() {
    let id_group_default: any = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
    let required: any = this.type == 8 ? Validators.compose([Validators.required]) : ""
    this.addExpensesForm = new FormGroup({
      date: new FormControl(new Date().toISOString()),
      customer: new FormControl("", Validators.compose([Validators.required])),
      total: new FormControl("", Validators.compose([Validators.required, Validators.pattern("(^[0?=[.]] |^[^.,-][,0-9]*)(([.](?=[0-9]){1}[0-9]{0,5})|)")])),
      content: new FormControl(""),
      payment_type: new FormControl('1'),
      id_branch: new FormControl(this.vhQuerySales.getDefaultBranch()._id),
      wallet: new FormControl(null),
      type_receipt: new FormControl(this.type),
      id_group: new FormControl(id_group_default.length ? id_group_default[0]._id : null, required)
    });
  }
  /**
   * check validate hp l
   * @param $event date
   */
  changeDate(event) {
    this.vhQuerySales.getValidityDate(event)
      .then((validitydate: any) => {
        if (validitydate) {
          this.vhComponent.alertMessageDesktop("warning", `${this.languageService.translate("You can only create or edit the invoice from the date")} ${this.fncService.formatDate(validitydate)}`, 8000)
          this.addExpensesForm.controls['date'].setValue(new Date())
        }
      })
  }

   /**Get tk ngn hng v gn vo bin listPaymentBank
   * @example this.getPaymentBankList()
  */
  getPaymentBankList() {
    let listPayment = this.vhQuerySales.getlocalWallets().filter(item => item._id != 'id_cash' && item.id_branch == this.vhQuerySales.getDefaultBranch()._id);
    this.listPaymentBank = listPayment.map(item => {
      return { value: item._id, label: item.name, isLeaf: true }
    })
  }

  /**bt sk thay i loi phiu chi, khi to li cc gi tr mc nh ca form
   * @example this.chooseType(1)
  */
  chooseType(value) {
    this.initForm();
  }


 
  /**Khi to cc trng chun ca bill  lu DB
   * @example  const bill = this.exportBill(bill, '070623F001')
   * @returns bill
  */
  exportBill(formValue, bill_code) {
    const bill: any = {
      bill_code: bill_code,
      date: new Date(formValue["date"]),
      id_employee: this.billInfo['employee']._id,
      id_branch: this.vhQuerySales.getDefaultBranch()._id,
      bill_type: parseInt(this.type),
      content: formValue["content"],
      payment_type: formValue["payment_type"],
      tax: 0,
      total: parseFloat(this.money_currency.getRawValue()),
      payment: parseFloat(this.money_currency.getRawValue())
    }
    if (bill['payment_type'][0] == '3') {
      bill['id_wallet'] = bill['payment_type'][1];
      bill['payment_type'] = 3;
    } else {
      bill['payment_type'] = 1;
      bill['id_wallet'] = 'id_cash';
    }
    if (this.type == 12) {
      bill['id_supplier'] = formValue['customer'],
        this.billInfo['supplier'] = this.vhQuerySales.getlocalSupplier(formValue['customer'])
    }
    else {
      bill.name = formValue["customer"]
      bill.id_group = formValue["id_group"]
    }
    return bill;


  }

  /**Hm to phiu chi */
  addExpenses(print) {
    
    
    Promise.all([
      this.vhQuerySales.createBillCode(new Date(this.addExpensesForm['value']["date"]),this.type),
      this.vhComponent.showLoading("")
    ]).then(([bill_code, loading]) => {
      const bill = this.exportBill(this.addExpensesForm['value'],bill_code)
      this.vhQuerySales
        .createBill(bill)
        .then((res: any) => {
          this.billInfo = { ...this.billInfo, ...bill, id: res._id }
          this.vhComponent.showToast(1500, (this.languageService.translate("Expenses has been created successfully")));
          this.vhComponent.hideLoading(0).then(() => {
            if (print) this.showPrint();
            else setTimeout(() => this.goBack(true), 200)
          })
        }, err => { })
    })
  }

  /** tr v trang danh sch phiu chi
   * @example this.goBack()
   */
  goBack(value?) {
    let data = { ...this.dataRestore }
    data.check = true
    this.router.navigate(['/sales/dashboard/manage/cashflow/expenses'], {
      state: value ? data : this.dataRestore
    });
  }

  /**Zorro modal save*/
  isPrint: boolean
  isVisible = false;
  /**
   * m modal hin th xc nhn to phiu chi
   * @param value : bin c check in hay ko
   * @example this.showModal(true)
   */
  showModal(value): void {
    this.isVisible = true;
    this.isPrint = value
  }
  /**
   * bt sk nhn nt ok ca modal
   * @example this.showModal(true)
   */
  handleOk(): void {
    this.isVisible = false;
    this.addExpenses(this.isPrint);
  }
  /**
   * bt sk nhn nt hy ca modal
   * @example this.showModal(true)
   */
  handleCancel(): void {
    this.isVisible = false;
  }

   /** kim tra d liu my in v show component in tng ng
   * @example this.showPrint() 
   */
  showPrint() {
    this.checkPrint().then((printer) => {
      if (printer) {
        this.vhComponent.showModal(this.renderPrintPage(printer['_id']), { billInfo: this.billInfo, printer: printer }, false, false, `modal-print-${printer['_id']}`)
          .then(modal => {
            modal.onWillDismiss().then(dataReturn => { this.goBack(true) })
          })
      }
    }, err => { this.goBack(true) })
  }

  /**
   * tr v component ca trang in tng ng
   * @example this.renderPrintPage('print_size_a4')
   * @returns component
   */
  private renderPrintPage(type: 'print_size_a4' | 'print_size_a5' | 'print_size_k80_4c' | 'print_size_k80_3c' | 'print_size_k57_3c') {
    switch (type) {
      case 'print_size_k57_3c': return ExpenseK57Col3Component
      case 'print_size_k80_3c': return ExpenseK80Col3Component
      case 'print_size_k80_4c': return ExpenseK80Col4Component
      case 'print_size_a5': return ExpensesA5Component
      case 'print_size_a4': return ExpenseA4Component
      default: return ExpenseA4Component
    }
  }

  /*----------Print------------------*/
   /**
   * ly thng tin d liu trang in
   * @returns Promise<any>
   * @example this.checkPrint()
   */
  checkPrint() {
    return new Promise((resolve, rejects) => {
      let printer = this.vhQuerySales.getLocalPrintPage('page_desktop_expense')
      if (printer && printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)].enable) resolve(printer.print_sizes[printer.print_sizes.findIndex(e => e._id == printer.print_size_default)])
      else rejects("No")
    })
  }


  /***********Thm xa sa nhm chi********** */
  isVisiblelist: boolean = false; // popup danh sch nhm
  activeButton: boolean = false
  /**
   * to nhm chi ln DB
   * @param nameGroup : tn nhm chi khc
   * @example this.addGroup('Tin mua ni tht')
   */
  addGroup(nameGroup): void {
    this.activeButton = true
    let index = this.expendGroup.findIndex(item => item.default)
    let value = {
      name: nameGroup.value,
      default: index != -1 ? false : true
    }
    if (value.name != "") {
      this.vhQuerySales.addSpending_group(value).then((res) => {
        this.expendGroup = [...this.expendGroup, res];
        this.nameGroup.nativeElement.value = ' ';
        this.activeButton = false
      })
    } else this.vhComponent.alertMessageDesktop("warning", "Cha nhp tn nhm")
  }

  /**
   * Cp nht trng default ca nhm chi
   * @param item : nhm chi  chn
   * @example this.setDefault(spending_group)
   */
  setDefault(item) {
    let index = this.expendGroup.findIndex(item => item.default)
    item.default = true
    if (index != -1) {
      this.expendGroup[index].default = false;
      Promise.all([this.vhQuerySales.updateSpending_group(item._id, item), this.vhQuerySales.updateSpending_group(this.expendGroup[index]._id, this.expendGroup[index])])
        .then(() => { })
    } else {
      this.vhQuerySales.updateSpending_group(item._id, item).then(() => { })
    }
  }


  /**
   * m modal chn nhm chi
   * @example this.openLisGroup()
   */
  openLisGroup() {
    let id = this.expendGroup.length ? this.expendGroup.filter(item => item.default == true) : []
    this.isVisiblelist = true
    this.addExpensesForm['value'].id_group = id.length ? id[0]._id : null
  }

  /**
   * tt modal chn nhm chi
   * @example this.handleCancellist()
   */
  handleCancellist() {
    this.isVisiblelist = false;
  }

  changeEdit: any = ''
  /**
   * gn _id ca nhm chi  chn vo bin changeEdit  update css  giao din
   * @param _id : _id ca nhm chi  chn
   */
  editGroup(_id) {
    this.changeEdit = _id
  }
  /**
   * cp nht tn nhm chi
   * @param item nhm chi  chn
   * @example this.saveEditGroup(item)
   */
  saveEditGroup(item) {
    let value = {
      name: item.name
    }
    this.vhQuerySales.updateSpending_group(item._id, value).then(() => {
      this.changeEdit = ""
    })
  }

  /**
   * xa nhm chi ln DB v local
   * @param _id : _id ca nhm chi  chn
   * @param index : v tr ca nhm chi trong mng local
   * @example this.deleteGroup('BDUWYHDIWUFRIWUHDE', 1)
   */
  deleteGroup(_id, index) {
    this.vhQuerySales.deleteSpending_group(_id).then((bool) => {
      if (bool) {
        this.expendGroup.splice(index, 1)
        this.initForm()
      }
      else this.vhComponent.alertMessageDesktop("error", "Khng xa c nhm ny")
    })
  }

}
